.loader-container {
    width: 100%;
    height: 47vh;
    position: relative;
    background: rgba(0, 0, 0, 0.05) no-repeat;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.spinner {
    width: 64px;
    height: 64px;
    border: 8px solid;
    border-color: #00b301 transparent #00b301 transparent;
    border-radius: 50%;
    animation: spin-anim 1.2s linear infinite;
}

@keyframes spin-anim {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
